@charset "utf-8";
html {
  font-size: 625%;
}
html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
body {
  font-size: .14rem;
  color: #e1ecee;
  background: url("../images/bg.png");
  background-size: 100% 100%;
}
html,
body,
div,
table,
ul,
li,
ol,
dl,
dd,
dt,
a,
img,
label,
p,
h1,
h2,
h3,
h4,
h5,
h6,
span {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}
input {
  margin: 0;
  padding: 0;
}
ul,
ol {
  list-style: none;
}
strong {
  font-weight: normal;
}
em,
i {
  font-style: normal;
}
img {
  max-width: 100%;
  display: block;
}
textarea {
  resize: none;
}
form {
  margin: 0;
}
a {
  color: #edfdff;
}
a,
a:hover,
a:focus {
  text-decoration: none;
  outline: none;
}
a:hover,
a:focus {
  color: #2bc1bf;
}
.wrap {
  width: 100%;
  margin: 0 auto;
}
.art-wrap {
  max-width: 1200px;
  width: 80%;
  margin: 0 auto;
}
/* 报道详情 start */
.wrap-top {
  z-index: 1000;
  position: relative;
  height: .35rem;
  padding-right: .1rem;
  margin-top: -0.35rem;
}
.arows-up {
  display: inline-block;
  width: .22rem;
  height: .22rem;
  border-radius: 50%;
  text-align: center;
  line-height: .22rem;
  vertical-align: bottom;
  margin-left: .15rem;
  cursor: pointer;
  background: #fff;
  color: #806b32;
}
.report-details {
  width: 100%;
  height: 100%;
  background: #013879;
}
.report-de-top {
  height: .9rem;
  padding: .1rem;
  background: rgba(255, 255, 255, 0.1);
}
.report-de-top p {
  display: inline-block;
  padding: .1rem .25rem;
  font-size: .18rem;
}
.report-de-top .arows-up i {
  margin-left: .02rem;
  color: #22528a;
}
.report-de-main {
  padding: .35rem .2rem;
}
.report-main-title {
  height: .9rem;
  margin: 0 1rem;
  text-align: center;
  border-bottom: 1px solid #315d92;
}
.report-main-title h3 {
  font-size: .38rem;
}
.report-main-title .title-bottom {
  width: 38%;
  margin: 0 auto;
  padding: .15rem .25rem;
}
.report-main-title .title-bottom p span {
  margin-right: .2rem;
}
.report-main-title .title-bottom .floatRight {
  float: right !important;
}
.pull-left {
  float: left;
}
.report-main-con {
  height: 7rem;
  padding: 0 1rem;
}
.report-main-con .con-txt {
  padding: .2rem 0 .35rem;
  overflow: hidden;
}
.report-main-con .con-txt p {
  text-indent: 30px;
  line-height: .3rem;
}
.report-main-con .main-msg {
  text-align: center;
}
.report-main-con .main-msg li {
  width: 2.6rem;
  height: 1.6rem;
  display: inline-block;
  margin: 0 .3rem .1rem 0;
  overflow: hidden;
}
.report-main-con .main-msg li img {
  width: 100%;
  height: 100%;
}
/* 报道详情 end */
/* 专题配置 start */
.bgw {
  background: #013879;
}
.project-con {
  padding: 0 .35rem;
  height: 99%;
}
.project-event {
  margin: .1rem 0 .3rem;
}
.project-event span {
  font-size: .18rem;
  margin-right: .25rem;
}
.project-event input {
  width: 5.15rem;
}
.tableWrap {
  /* height: 80.5%; */
  /* height: 86%; */
  max-height: 8rem;
  overflow: hidden;
}
.pro-table {
  width: 100%;
  padding: 0 .4rem;
  background: rgba(255, 255, 255, 0.15);
}
.pro-table td:nth-child(1) {
  width: 25%;
}
.pro-table td:nth-child(2) {
  width: 40%;
}
.pro-table td:nth-child(3) {
  width: 20%;
}
.pro-table td:nth-child(4) {
  max-width: 15%;
}
/* 翻页 start */
.page-box {
  margin-top: .25rem;
}
.page-box span {
  display: inline-block;
}
.page-box .btn1 {
  display: inline-block;
  line-height: .22rem;
  min-width: .22rem;
  text-align: center;
  padding: 0 8px;
  background: rgba(255, 255, 255, 0.22);
  margin: 0 2px;
}
.page-box .btn1:hover,
.page-box .btn1.active {
  background: #0a5ab3;
  color: #fff;
}
.page-box .page-list {
  display: inline-block;
  vertical-align: middle;
}
.page-box .page-list li {
  float: left;
}
.page-box .dropup {
  margin-left: .1rem;
  margin-right: .05rem;
}
.page-box input {
  display: inline-block;
  width: .5rem;
  margin: 0 .1rem;
  text-align: center;
  background: rgba(255, 255, 255, 0.22);
  border: none;
}
/* 翻页 end */
/** 弹出框 start */
/*改变dialong样式开始*/
.modal-dialog {
  margin: 0 auto;
}
.modal-content {
  box-shadow: 0 0 10px #013879;
}
.ui-dialog-grid {
  color: white;
}
.ui-dialog {
  background: #013879 !important;
}
.ui-dialog-header {
  color: white;
  border-bottom: 1px solid #3e699f !important;
}
.ui-dialog-close {
  color: white !important;
  opacity: 1 !important;
}
.ui-dialog-button {
  float: none !important;
  /* margin-left: 4.5rem !important; */
  text-align: center;
}
.ui-dialog-footer button {
  width: 1.2rem !important;
  height: .44rem !important;
}
.ui-dialog-footer button.ui-dialog-autofocus {
  margin-left: .2rem !important;
  background: #0f6cde !important;
}
.ui-dialog-footer button.ui-dialog-autofocus:hover {
  background: #1066d0 !important;
}
.ui-dialog-content {
  overflow: auto;
  padding-right: .2rem;
}
.ui-dialog-body {
  padding: 20px 0 20px 20px !important;
}
.ui-dialog-title {
  padding-left: .3rem !important;
}
.ui-dialog-title:before {
  position: absolute;
  left: .08rem;
  top: .12rem;
  content: '';
  width: .08rem;
  height: .32rem;
  background: #44daff;
}
/*改变dialong样式结束*/
#editReport {
  display: none;
}
.modal-list > li {
  margin-bottom: .2rem;
}
.modal-list > li label {
  float: left;
  width: 1.6rem;
  text-align: right;
  font-size: .2rem;
