}
.modal-list > li .list-right .span-txt {
  color: #7dadef;
  margin-left: .15rem;
}
.modal-list > li .list-right .list-checked > li {
  float: left;
}
.modal-list > li .list-right .list-checked > li label {
  text-align: left;
  font-size: .14rem;
  font-weight: normal;
}
.modal-list .des-con {
  display: none;
}
.modal-list .des-con li {
  margin-bottom: .2rem;
}
.report-box li {
  margin-bottom: .2rem;
}
.report-box li .report-con {
  padding: .2rem .24rem;
  background: rgba(255, 255, 255, 0.03);
}
.report-box li .report-con .con-span {
  float: left;
  display: inline-block;
}
.report-box li .report-con .list-right {
  margin-left: .95rem;
}
.report-box li:last-child {
  margin-bottom: 0;
