  padding: .06rem 0 0 .9rem;
}
.edit-more-btn .addContent .aControl a {
  border: none;
}
.edit-more-btn .addContent .aControl .cancel {
  background: white;
  color: #000000;
}
.edit-more-btn .addContent .aControl .save {
  background: #0f6cde;
  color: #ffffff;
}
.layut-times input {
  display: inline-block;
  width: 33.5% !important;
  padding: 0 .15rem;
}
.layut-times .time-bg {
  background: url(../images/times.png) no-repeat 2.9rem center;
  background-color: #fff;
}
.layut-file {
  position: relative;
}
.layut-file > li {
  float: left;
}
.layut-file > li .upload-list {
  padding: 0 .3rem 0 .17rem;
}
.layut-file > li .upload-list li {
  margin-bottom: .1rem;
}
.layut-file > li .upload-list li .pro-box {
  display: inline-block;
  margin: 0 .65rem 0 1rem;
}
.layut-file > li .upload-list li .pro-box .pro-percent {
  position: relative;
  display: inline-block;
  width: 1.6rem;
  height: .06rem;
  border-radius: 20px;
  margin-right: .08rem;
  background: #fff;
}
.layut-file > li .upload-list li .pro-box .pro-percent i {
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: auto;
  height: 100%;
  border-radius: 20px;
  background: #0f6cde;
}
.layut-file > li .upload-list li .pro-box .uploading {
  margin-left: .4rem;
}
.layut-file > li .upload-list li img {
  cursor: pointer;
}
.layut-file a {
  margin: 0;
  background: #0f6cde;
}
.layut-file input {
  position: absolute;
  top: 0;
  left: 0;
  width: 15%;
  height: 100%;
  opacity: 0;
  font-size: 0;
  cursor: pointer;
}
/** 弹出框 end */
/*上传图片开始*/
#uploader .filelist {
  list-style: none;
  margin: 0;
  padding: 0;
}
#uploader .filelist:after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  overflow: hidden;
  clear: both;
}
#uploader .filelist li {
  width: 110px;
  height: 110px;
  /*background: url(./bg.png) no-repeat;*/
  background: url(../images/image-upload/bg.png) no-repeat;
  text-align: center;
  /*margin: 0 8px 20px 0;*/
  margin: 0 10px 15px 0;
  position: relative;
  display: inline;
  float: left;
  overflow: hidden;
  font-size: 12px;
}
#uploader .filelist li p.log {
  position: relative;
  top: -45px;
}
#uploader .filelist li p.title {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  top: 5px;
  text-indent: 5px;
  text-align: left;
}
#uploader .filelist li p.progress {
  position: absolute;
  width: 100%;
  bottom: -20px;
  left: 0;
  height: 8px;
  overflow: hidden;
  z-index: 50;
}
#uploader .filelist li p.progress span {
  display: none;
  overflow: hidden;
  width: 0;
  height: 100%;
  /*background: #1483d8 url(./progress.png) repeat-x;*/
  background: #1483d8 url(../images/image-upload/progress.png) repeat-x;
  -webit-transition: width 200ms linear;
  -moz-transition: width 200ms linear;
  -o-transition: width 200ms linear;
  -ms-transition: width 200ms linear;
  transition: width 200ms linear;
  -webkit-animation: progressmove 2s linear infinite;
  -moz-animation: progressmove 2s linear infinite;
  -o-animation: progressmove 2s linear infinite;
  -ms-animation: progressmove 2s linear infinite;
  animation: progressmove 2s linear infinite;
  -webkit-transform: translateZ(0);
}
@-webkit-keyframes progressmove {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 17px 0;
  }
}
@-moz-keyframes progressmove {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 17px 0;
  }
}
@keyframes progressmove {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 17px 0;
  }
}
#uploader .filelist li p.imgWrap {
  position: relative;
  z-index: 2;
  line-height: 110px;
  vertical-align: middle;
  overflow: hidden;
  width: 110px;
  height: 110px;
  -webkit-transform-origin: 50% 50%;
  -moz-transform-origin: 50% 50%;
  -o-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  -webit-transition: 200ms ease-out;
  -moz-transition: 200ms ease-out;
  -o-transition: 200ms ease-out;
  -ms-transition: 200ms ease-out;
  transition: 200ms ease-out;
}
#uploader .filelist li img {
  width: 100%;
}
#uploader .filelist li p.error {
  background: #f43838;
  color: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
  height: 28px;
  line-height: 28px;
  width: 100%;
  z-index: 100;
}
#uploader .filelist li .success {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 40px;
  width: 100%;
  z-index: 200;
  /*background: url(./success.png) no-repeat right bottom;*/
  background: url(../images/image-upload/success.png) no-repeat right bottom;
}
#uploader .filelist li .close {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 300;
  color: white;
  opacity: 1;
}
#uploader .filelist div.file-panel {
  position: absolute;
  height: 0;
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, startColorstr='#80000000', endColorstr='#80000000');
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 300;
}
#uploader .filelist div.file-panel span {
  width: 24px;
  height: 24px;
  display: inline;
  float: right;
  text-indent: -9999px;
  overflow: hidden;
  /*background: url(./icons.png) no-repeat;*/
  background: url(../images/image-upload/icons.png) no-repeat;
  margin: 5px 1px 1px;
  cursor: pointer;
}
#uploader .filelist div.file-panel span.rotateLeft {
  background-position: 0 -24px;
}
#uploader .filelist div.file-panel span.rotateLeft:hover {
  background-position: 0 0;
}
#uploader .filelist div.file-panel span.rotateRight {
  background-position: -24px -24px;
}
#uploader .filelist div.file-panel span.rotateRight:hover {
  background-position: -24px 0;
}
#uploader .filelist div.file-panel span.cancel {
  background-position: -48px -24px;
}
#uploader .filelist div.file-panel span.cancel:hover {
  background-position: -48px 0;
}
#filePicker {
  width: .9rem;
  height: .3rem;
  background: #0f6cde;
  text-align: center;
  line-height: .3rem;
  border-radius: 3px;
  cursor: pointer;
}
